@import "src/globalStyle/vars";

.bg {
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 9999;
	width: 100%;
	height: 100vh;
	background-color: $title;
	transition: all $time;
	overflow: hidden;
	&.__hide {
		animation: hide 0.9s ease-in-out 0s 1 forwards;
	}
}

@keyframes hide {
	0% {
		opacity: 1;
		overflow: visible;
	}
	99% {
		opacity: 0;
		overflow: hidden;
		bottom: 0;
	}
	100% {
		bottom: 100%;
	}
}
